
function push({ path, params, name }) {
    window.api.openWin({
        name: name,
        url: path,
        pageParam: params
    });
}
function replace({ path, params, name }) {
    window.api.openWin({
        name: name,
        url: path,
        pageParam: params
    });
}
function back() {
    window.api.closeWin({})
}
export default {
    push,
    replace,
    back
}


